import React, {useEffect, useState} from 'react';
import {getOne} from "../api/Api";

function API_1(props) {


    const [serverData, setServerData] = useState('')

    useEffect(() => {

        getOne().then(data => {
            console.log(data)
            setServerData(data)
        })
    }, []);

    return (

        <div>
            <div>API_1 Page 입니다.</div>
            <div>${serverData}</div>

        </div>


    )
}

export default API_1;